<template>
    <div class="hello" style="height: 100%">
        <Carousel :carousel="carousel" :songlist.sync="selectSongList" height="300px" ></Carousel>

        <div style="background-color: #fff;height: calc( 100% - 300px);">
            <div style="line-height: 20px;background-color: aliceblue;">

            </div>
            <div style="height: calc(100% - 20px);overflow: auto;">
                <div v-for="songItem  in songList" class="song-menu-item" :key="songItem.name" @click="clickSongItem(songItem.id)"
                     @contextmenu="contextmenu($event,songItem)">
                    <el-image
                            style="width: 50px; height: 50px"
                            :src="songItem.pic"
                            fit="cover"></el-image>
                    <span style="padding: 0 5px">{{songItem.name}}</span>
                </div>
            </div>

        </div>

        <transition name="fade" >
            <div ref="contextmenu" :style="contextMenuStyle"
                 style="line-height: 18px;background-color: #fff;padding: 2px" v-show="contextMenuShow"></div>
        </transition>

    </div>
</template>

<script>
    import Carousel from "./Carousel.vue"

    export default {
        props:["categoryId"],
        data() {
            return {
                //1 默认 2 最新 3 热门 4 评分
                sortArr: [],
                activeIndex: '0',
                songList: null,
                selectSongList: null,
                contextMenuStyle: {
                    position: "fixed",
                    top: "",
                    left: "",

                },
                contextMenuShow: false,

            }
        },
        mounted() {
            var self = this;
            document.querySelector("body").addEventListener("click", function () {
                self.contextMenuShow = false;
            }, false);
        },
        methods: {
            clickSongItem(songListId) {
                this.musicApi.songlist(songListId).then(data => {
                    this.$emit("update:song-list", data.data.data);
                })
            },
            //1 默认 2 最新 3 热门 4 评分
            handleSelect(e) {
                var self = this;
                this.musicApi.hotSongList({
                    sortId: Number(e) + 1
                }).then(data => {
                    self.songList = data.data.data;
                })
            },
            contextmenu(e, songInfo) {
                var x = e.clientX;
                var y = e.clientY;
                this.$refs.contextmenu.innerHTML = "";
                // for (let prop in songInfo) {
                //     if (songInfo.hasOwnProperty(prop)) {
                //
                //         this.$refs.contextmenu.innerHTML += `<span>${prop} :</span><span> ${songInfo[prop]} </span>`
                //     }
                // }
                this.$refs.contextmenu.innerHTML += `<span></span><span> ${songInfo.name} </span><br/>`

                this.contextMenuShow = true;
                this.contextMenuStyle.top = y + "px";
                this.contextMenuStyle.left = x + "px";
                e.preventDefault();
            }

        },
        watch: {
            selectSongList(data) {
                this.$emit("update:song-list", data.data.data);
            },
            categoryId () {
                var self = this;
                if(!self.categoryId)return;
                this.musicApi.hotSongList({
                    categoryId: self.categoryId
                }).then(data => {
                    self.songList = data.data.data;
                })
            }
        },
        computed: {
            carousel() {
                if (this.songList) {
                    return this.songList.slice(0, 4);
                } else {
                    return null;
                }
            },
        },

        components: {
            Carousel
        }
    }
</script>

