    .hello {
        height: 100%;
        overflow: hidden;
    }

    .song-menu-item {
        height: 50px;
        line-height: 50px;
        text-align: left;
        margin: 5px;
        background-color: antiquewhite;
        transition: all 200ms ease-out;
        padding: 3px ;
    }

    .song-menu-item:hover {
        background-color: #67C23A;
    }

    .fade-enter-active, .fade-leave-active {
        transition: all 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }
</style>
