<template>
    <div>
        <div v-if="songList">
            <div  v-for="(song,index )  in songList.songs" :key="index"
                  @click="handleSongItem(index)"
                  :class="[playerIndex === index ? 'player-item': '','list-item']"
                  style="position: relative">
                <el-image
                        style="width: 50px; height: 50px"
                        :src="song.pic"
                        fit="cover"></el-image>
                <span style="padding-left: 30px">名字</span>:
                <span>{{song.name}}</span>
                <i class="el-icon-video-play "
                   v-if="playerIndex === index "
                   style="float: right;margin: 19px 100px 0 0">

                </i>

            </div>
        </div>
        <div v-else class="list-empty-icon">
            <i class="el-icon-s-shop"></i><br/>
            播放列表为空,选择歌单
        </div>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    export default {
        name: "AudioList",
        props: ["songList"],
        data () {
            return {

            }
        },
        methods: {
            handleSongItem (songUrl) {
                this.$store.commit("updatePlayerIndex",songUrl);
            }
        },
        watch: {
            songList () {
                this.$store.commit("updateplayerList",this.songList);
            }
        },
        computed: mapState({
            playerIndex (state) {
                return state.playerIndex;
            }
        })
    }
</script>

<style scoped>
    .list-item{
        height: 50px;
        line-height: 50px;
        text-align: left;
        padding: 3px 15px;

    }
    .list-empty-icon{
        margin-top: 150px;
    }
    .list-item *{
        cursor: pointer;
    }
    .list-item:hover{
        background-color: #67C23A;
    }
    .player-item{
        background-color: #65ff81;
    }
    .el-icon-video-play{
        position: absolute;
        left: 73px;
        top: 2px;
    }
</style>